import { toast } from "vue3-toastify";
import { ToastPosition } from "vue3-toastify";
import { ToastTheme } from "vue3-toastify";
import { ToastTransition } from "vue3-toastify";

const defaultOptions = {
    theme: "light" as ToastTheme,
    position: "top-center" as ToastPosition,
    transition: "flip" as ToastTransition,
}

const notification = {    
    success: ((msg: string) => {
        toast(msg, {
            ...defaultOptions, 
            type: "success",            
        });
    }),
    error: ((msg: string) => {
        toast(msg, {
            ...defaultOptions,
            type: "error",            
        });
    }),
    warning: ((msg: string) => {
        toast(msg, {
            ...defaultOptions,
            type: "warning",            
        });
    })
}

export default notification;