<template>
  <input
    class="input__phone"
    v-if="getTypeInput === 'phone'"
    type="phone"
    v-phone-mask
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.disabled"
    :maxlength="props.maxlength"
  />

  <input
    class="input__card"
    v-else-if="getTypeInput === 'card'"
    type="text"
    v-card-number
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.disabled"
    :maxlength="props.maxlength"
  />

  <input
    class="input__date"
    v-else-if="getTypeInput === 'date'"
    type="text"
    v-date
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.disabled"
    :maxlength="props.maxlength"
  />

  <input
    class="input__time"
    v-else-if="getTypeInput === 'time'"
    type="text"
    maxlength="5"
    v-time
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.disabled"
  />

  <input
    class="input__number"
    v-else
    type="number"
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.disabled"
    :maxlength="props.maxlength"
  />
</template>

<script lang="ts" setup>
import { vPhoneMask } from "@/directives/ValidationPhoneDirective";
import { vFocusClass } from "@/directives/FocusDirective";
import { vCardNumber } from "@/directives/CardNumberDirective";
import { vDate } from "@/directives/DateDirective";
import {
  defineProps,
  computed,
  ref,
  defineEmits,
  onMounted,
  watch,
  withDefaults,
} from "vue";
import vTime from "@/directives/TimeDirective";

const emit = defineEmits(["input", "update:modelValue", "update:is-valid"]);

type InputTypeValue = "number" | "phone" | "date" | "card" | "time";

const props = withDefaults(
  defineProps<{
    modelValue: number | string | null;
    placeholder?: string;
    isValid?: boolean | null;
    type?: InputTypeValue;
    disabled?: boolean;
    maxlength?: number;
  }>(),
  {
    isValid: true,
    type: "number",
    disabled: false,
    maxlength: 50,
  }
);

const numberValue = ref<number | string | null>(
  !props.modelValue ? "" : props.modelValue
);

const MAX_LENGTH_PHONE = 11;

const getTypeInput = computed((): string => {
  return props.type;
});

const getPlaceholderInput = computed((): string => {
  return props.placeholder || "число";
});

function applyPhoneMask(value: string): string {
  const matrix = "+7 (___) ___-__-__";
  const def = matrix.replace(/\D/g, "");
  const val = value.replace(/\D/g, "");

  let i = 0;

  const newValue = matrix.replace(/[_\d]/g, (a) => {
    return i < val.length ? val.charAt(i++) || def.charAt(i) : a;
  });

  i = newValue.indexOf("_");
  if (i !== -1) {
    i < 5 && (i = 3);
    return newValue.slice(0, i);
  }

  return newValue;
}

const setNewValue = () => {
  const valueAsString = String(numberValue.value);

  if (getTypeInput.value === "phone") {
    const digits = valueAsString.replace(/\D/g, "").slice(0, MAX_LENGTH_PHONE);
    const isValid = digits.length === MAX_LENGTH_PHONE;
    emit("update:modelValue", digits);
    emit("update:is-valid", isValid);
  } else if (getTypeInput.value === "number") {
    const isValid = valueAsString.length > 0;
    emit("update:is-valid", isValid);
    emit("update:modelValue", Number(valueAsString));
  } else {
    emit("update:modelValue", valueAsString);
  }
};

watch(
  () => props.modelValue,
  (newValue) => {
    if (newValue !== null) {
      if (getTypeInput.value === "phone" || getTypeInput.value === "whts") {
        const maskedValue = applyPhoneMask(String(newValue));
        numberValue.value = maskedValue;
      } else {
        numberValue.value = String(newValue);
      }
    }
  }
);

watch(
  () => numberValue.value,
  (newValue) => {
    const isValid = newValue !== "" && newValue !== null;
    if (getTypeInput.value !== "phone") {
      emit("update:is-valid", isValid);
    }
  }
);

onMounted(() => {
  if (props.modelValue !== null) {
    if (getTypeInput.value === "phone" || getTypeInput.value === "whts") {
      const maskedValue = applyPhoneMask(String(props.modelValue));
      numberValue.value = maskedValue;
    } else {
      numberValue.value = String(props.modelValue);
    }
  }

  const isValid = String(numberValue.value).length > 0;
  emit("update:is-valid", isValid);
});
</script>

<style scoped lang="scss">
@import url(../../styles/variable.css);

input[type="text"] {
  caret-color: var(--yellow);
}
input:disabled {
  color: var(--light-gray);
}
input[type="number"] {
  -moz-appearance: textfield;
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}
</style>
