<template>
  <input
    class="input__text"
    type="text"
    :value="modelValue"
    :placeholder="placeholder || 'Текст'"
    v-focus-class
    @input="updateValue"
    @focus="updateValue"
    :disabled="disabled"
  />
</template>

<script lang="ts" setup>
import { defineProps, defineEmits } from "vue";
import { vFocusClass } from "@/directives/FocusDirective";

// Определяем пропсы
const props = defineProps({
  modelValue: {
    type: String,
    required: true,
    default: "",
  },
  isValid: {
    type: Boolean,
    required: false,
    default: true,
  },
  placeholder: {
    type: String,
    required: false,
  },
  propertyName: {
    type: String,
    required: false,
  },
  disabled: {
    type: Boolean,
    required: false,
    default: false,
  },
});

const emit = defineEmits(["update:modelValue", "update:is-valid"]);

const updateValue = (event: Event) => {
  const target = event.target as HTMLInputElement;
  const newValue = target.value;
  const isValid = newValue.length > 0;

  emit("update:modelValue", newValue);
  emit("update:is-valid", isValid);
};
</script>

<style scoped lang="scss">
@import url(../../styles/variable.css);

input[type="text"] {
  caret-color: var(--yellow);
}
</style>
