import { DirectiveBinding } from 'vue';

export const vCardNumber = {
  mounted(el: HTMLInputElement, binding: DirectiveBinding) {
    el.addEventListener('input', (event: Event) => {
      const input = event.target as HTMLInputElement;
      let value = input.value.replace(/\s+/g, ''); // Удаляем все пробелы

      // Ограничиваем ввод до 16 цифр
      if (value.length > 16) {
        value = value.slice(0, 16);
      }

      // Добавляем пробел после каждых 4 цифр
      value = value.replace(/(\d{4})/g, '$1 ').trim();

      // Обновляем значение в инпуте
      input.value = value;

      // Добавляем или удаляем класс в зависимости от валидности
      if (value.replace(/\s+/g, '').length === 16) {
        el.classList.remove('invalid-input');
      } else {
        el.classList.add('invalid-input');
      }
    });

    // Обработка события blur (потеря фокуса)
    el.addEventListener('blur', (event: Event) => {
      const input = event.target as HTMLInputElement;
      const value = input.value.replace(/\s+/g, '');

      if (value.length !== 16) {
        el.classList.add('invalid-input');
      } else {
        el.classList.remove('invalid-input');
      }
    });
  },
};